/* eslint-disable */
import react, { useEffect, useState } from 'react'
import { Tabs, Tab } from '@antmjs/vantui'

export default function Demo() {
  const [state, setState] = useState(['内容1', '内容2', '内容3'])
  useEffect(() => {
    setTimeout(() => {
      setState(['内容1', '内容2', '内容3', '内容1', '内容2', '内容3'])
    }, 3000)
  }, [])
  return (
    <Tabs active={1}>
      {state.map((item, i) => {
        return (
          <Tab title={item} key={i}>
            {item}
          </Tab>
        )
      })}
    </Tabs>
  )
}
